import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ApiLinkService } from '../shared/api-link.service';
import { AuthService } from '../guard/auth.service';

@Injectable({
  providedIn: 'root'
})
export class ShopingService {

  constructor(private http: HttpClient, private api: ApiLinkService, private auth: AuthService) { }

  serverPath: string = this.api.srvLink;

  headers = new HttpHeaders({
    'Content-Type': "application/json",
    'Authorization': this.auth.getLogged()
  });

  upHeaders = new HttpHeaders({
    'Authorization': this.auth.getLogged()
  });
  upHeaders1 = new HttpHeaders({

  });
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json'
    })
};

  uploadImg(data) {
    console.log(data);
    return this.http.post(this.api.delear+"api/comunication/image", data, {headers: this.upHeaders1, reportProgress: true, observe: 'events'})

  }

  uploadclassifiedImage (data) {
    return this.http.post(this.api.delear+"api/comunication/imageuploadclassified", data, {headers: this.upHeaders1, reportProgress: true, observe: 'events'})
  }
  addProduct (data, url) {
    return this.http.post(this.api.server+url, data, {headers: this.headers});
  }

  getAllProduct (data) {
    return this.http.post(this.api.delear+"api/product/allProduct", data, {headers: this.headers});
  }

  delProduct (data) {
    return this.http.post(this.api.server+'product/deleteProduct', data, {headers: this.headers});
  }

  myorder () {
    return this.http.get(this.api.server+'product/myOrder', {headers: this.headers});
  }

  acceptedInvoice (data) {

    return this.http.post(this.api.server+'product/updateStatus', data, {headers: this.headers});
  }

  InvoiceSend (data) {
    return this.http.post(this.api.server+'product/InvoiceSend', data, {headers: this.headers});
  }

  getSopkeeperInfo(data) {
    return this.http.post(this.api.server+"profileInformation", data,  {headers: this.upHeaders1});
  }

  updateDiscount (data) {
    return this.http.post(this.api.server+'addDiscount', data, {headers: this.headers});
  }
}
